<template>
    
    <div class="menu" :class="{'z-depth-5':animationCompleted}" v-click-outside="hide">
        <div @click="toggleMenu" class="menu-icon main" >
            <i class="mdi" :class="store.activeIcon" ></i>
        </div>

        <transition 
            appear
            enter-active-class="animated slideInRight" 
            leave-active-class="animated slideOutRight" 
            @after-enter="afterEnter" 
            @before-leave="afterLeave">
            
            <div class="card blue ligthen-2 z-depth-5" v-show="store.menuIsActive" >
                 <div @click="toggleMenu" class="menu-icon">
                    <i class="mdi mdi-arrow-right-bold" ></i>
                </div>

                <div class="card-content black-text">
                    <ul>
                        <li class="mdi mdi-account-circle white-text" @click="infoView"></li>
                        <li class="mdi mdi-code-tags white-text" @click="skillsView"></li>
                        <li class="mdi mdi-clipboard-text white-text" @click="jobsView"></li>
                        <!-- <li class="mdi mdi-email white-text contact" @click="jobsView"></li>-->
                    </ul>
                </div>
                <!-- <div class="card-action">
                // </div>-->
            </div>
        </transition>
    </div>
    
</template>

<script>
    import store from '../store'
    export default{
        data() {
            return {
                store: store.state,
                botResponse:'Hola!',
                animationCompleted:false
            }
        },
        methods:{
            toggleMenu(){
                this.store.menuIsActive = !this.store.menuIsActive
            },
            hide(){
                this.store.menuIsActive = false
            },
            skillsView(){
                this.store.activeSection = 'cvSkills'
                this.store.activeIcon = 'mdi-code-tags'
            },
            infoView(){
                this.store.activeSection = 'cvWhoIAm'
                this.store.activeIcon = 'mdi-account-circle'
            },
            jobsView(){
                this.store.activeSection = 'cvJobs'
                this.store.activeIcon = 'mdi-clipboard-text'
            },
            afterEnter(el){
                this.animationCompleted= true
            },
            afterLeave(el){
                this.animationCompleted= false
            }
        },
        directives: {
            'click-outside': {
            bind: function(el, binding, vNode) {
                // Provided expression must evaluate to a function.
                if (typeof binding.value !== 'function') {
                    const compName = vNode.context.name
                let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
                if (compName) { warn += `Found in component '${compName}'` }
                
                console.warn(warn)
                }
                // Define Handler and cache it on the element
                const bubble = binding.modifiers.bubble
                const handler = (e) => {
                if (bubble || (!el.contains(e.target) && el !== e.target)) {
                    binding.value(e)
                }
                }
                el.__vueClickOutside__ = handler

                // add Event Listeners
                document.addEventListener('click', handler)
                    },
            
            unbind: function(el, binding) {
                // Remove Event Listeners
                document.removeEventListener('click', el.__vueClickOutside__)
                el.__vueClickOutside__ = null

            }
            }
        }


    }
</script>

<style scoped>
    .menu{
        z-index:500;
        display:flex;
        position:fixed;
        height:100%;
        align-self:flex-end
    }

    .card{
        margin:0;
        width:130px;
        display:flex;
        flex-direction:column;
        overflow:hidden;;
        background-color:yellow
        
    }
    .card-content{
        position:relative;
        top:80px;
        width:130px;
        height:100%;
    }

    .menu-icon{
        position:absolute;
        font-size:2.2rem;
        top: 25px;
        right: 45px;
   }

   .menu-icon{
       cursor:pointer
   }

   .menu-icon.main{
       font-size:4.2rem
   }

   ul{
       position:relative;
       left:20px;
       height:100%;
   }
   li{
       margin-bottom:35px;
   }
   li.mdi{
       font-size:3rem
   }

   li:hover{
       cursor:pointer
   }

   li.contact{position:absolute;
        bottom:30px;
   }

   .slideOutRight {
        -webkit-animation: slideOutRight .3s; 
        -moz-animation:    slideOutRight .3s; 
        -o-animation:      slideOutRight .3s; 
        animation:         slideOutRight .3s; 
    }

     .slideInRight {
        -webkit-animation: slideInRight .3s; /* Safari 4+ */
        -moz-animation:    slideInRight .3s; /* Fx 5+ */
        -o-animation:      slideInRight .3s; /* Opera 12+ */
        animation:         slideInRight .3s; /* IE 10+, Fx 29+ */
    }
    
    @media (max-width: 600px){
        .menu-icon.main{
            font-size:35px;
            position:absolute;
            top: 15px;
            right: 25px;
        }
    }
</style>


