<template>
  <div id="app" class="blue darken-2">
    <cv-Header></cv-Header>
    <cv-Sidebar></cv-Sidebar>
    <div class="container">
      <transition
      mode="out-in"
      enter-active-class="animated fadeIn" 
      leave-active-class="animated fadeOut" >
        <component :is="store.activeSection"></component>
      </transition>

    </div>

  </div>
</template>

<script>
import store from './store.js'
import cvHeader from './components/Header'
import cvWhoIAm from './components/WhoIAm'
import cvSkills from './components/Skills'
import cvJobs from './components/Jobs'
import cvSidebar from './components/Sidebar'

export default {
  components:{
    cvHeader,
    cvWhoIAm,
    cvSkills,
    cvJobs,
    cvSidebar
  },
  name: 'app',
  data(){
    return {
      store:store.state
    }
  }
}
</script>

<style>

  ::-webkit-scrollbar {
    display: none !important;
    overflow: -moz-scrollbars-none !important;
  }
  body{
    background-color:#1976D2
  }
</style>

<style scoped>

#app{
  display: flex;
  height:100vh; 
  flex-direction:column;
  color:white;
}

.container{
  height:100%;
  display:flex;
  align-items:center;
  justify-content:center;
}

</style>



